
// progress bar -------------------------------------------

.progress {
	margin-top: 2em;
	display: flex;
	align-items: center;
	color: $secondaryTxt;
}

.progress_bar {
	width: 0;
	height: 0.625em;
	border-radius: 0.3125em;
	background: currentColor;
}



// nav filter ---------------------------------------------

.nav_filter {
	display: inline-block;
}



// tabmenu ------------------------------------------------

.tab-menu {
	display: inline-block;
	border-radius: 14px;
	margin: 0;
	padding: 1px;
	list-style: none;
	width: auto;
	height: 26px;	
	border-radius: 12px;
	background: rgb(255,255,255);
	font-size: 12px;
	line-height: 24px;
}

.tab-menu svg {
	width: 24px;
	height: 24px;
}

.tab-menu input {
	display: none;
}

.tab-menu label {
	// padding: 0 1em;
	display: block;
	float: left;
	width: 24px;
	height: 24px;
	color: $primaryTxt;
	border-radius: 12px;
	background: rgba(0,0,0,0.2);
}

.tab-menu label:not(:first-of-type) {
	margin-left: 1px;
}

.tab-menu input:checked + label {
	background: rgba(0,0,0,1);
}

.tab-menu .tab {
	border-radius: 12px;
	padding: 0 1em;
	color: $primaryBg;
	display: inline-block;
	text-transform: uppercase;
	-webkit-touch-callout: none;
	    -webkit-user-select: none;
	     -khtml-user-select: none;
	       -moz-user-select: none;
	        -ms-user-select: none;
	            user-select: none; 	
	& + .tab {
		margin-left: 1px;
	}
}

.tab-menu .tab:hover {
	color: $primaryTxt;
	background: $dataFocus;
}

.tab-menu .tab.active {
	color: $primaryTxt;
	background: $primaryBg;
}

@media only screen and (max-width: $minWidth - 1px){
	.nav_filter .tab-menu {
		background: transparent;
		.tab {
			color: $primaryTxt;
			border-radius: 24px;
			margin-bottom: 1em;
		}
	}

}



// gaze finder --------------------------------------------

.gazefinder {
	position:relative;
	padding-bottom: 100%;
}

#img {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
}

#img > img {
  object-fit: cover;
  border-radius: 50%;
  height: 100%;
  width: 100%;
}

#img > img:not([src]) {
   visibility: hidden;
}

#cursor{
	position: absolute;
	z-index: +1;
	top: -100px;
	left: -100px;
	background: rgba(255,0,0,0.2);
	border: 1px solid red;
	box-sizing: border-box;
	width: 3px;
	height: 3px;
	margin-left: -1.5px;
	margin-top: -1.5px;
	pointer-events: none;
}

#cursor::after{
	content: "";
	position: absolute;
	top: 50%;
	left: 50%;
	width: 2px;
	height: 2px;
	background: red;
}



