}



// face ---------------------------------------------------

.face {
	position: relative;
	float: left;
	margin: 0.75em;
	border-radius: 50%;
	width: 5em;
	height: 5em;
	font-size: 8pt;
	color: red;
}

.face:hover {
	z-index: +9;
}

.face .face-img {
	position: absolute;
  object-fit: cover;
  border-radius: 50%;
  height: 5em;
  width: 5em;
	background: red;
}

.face-caption {
	display: none;
}


