// reset --------------------------------------------------

* {
	box-sizing: border-box;
}

// fix variable scrollbars
html { 
	overflow-y: scroll; 
}

body {
	margin: 1.5em;
	font-family: 'IBM Plex Sans', sans-serif;
	color: $data;
	background-color: $secondaryBg;
}



// header -------------------------------------------------

.header {
	margin-bottom: 1.5em;
	margin-left: 0;
}



// nav ----------------------------------------------------

.nav {
	z-index: +9;
	display: flex;
	flex-direction: column;
	margin-bottom: 1.5em;
	@media only screen and (min-width: $minWidth) {
		flex-direction: row;
	}	
}

.nav_toggle {
	margin-bottom: 1.5em;
}

.nav_filter {
	margin-left: 0;
	@media only screen and (min-width: $minWidth) {
		margin-left: 1em;
	}	
}



// columns ------------------------------------------------

.columns {
	display: flex;
	flex-direction: column;
	@media only screen and (min-width: $minWidth) {
		margin-left: $leftMargin;
		flex-direction: row;
	}
}

.column_primary {
	margin-bottom: 1.5em;
	@media only screen and (min-width: $minWidth) {
		flex: 1;
	}
}

.column_secondary {
	@media only screen and (min-width: $minWidth) {
		flex: 1;
	}
}

.column_primary + .column_secondary {
	@media only screen and (min-width: $minWidth) {
		margin-left: 1.5em;
	}
}



// footer -------------------------------------------------

.footer {
	@media only screen and (min-width: $minWidth) {
		margin-left: 3em;
	}
}



// widget -------------------------------------------------

#widget {
	position: relative;
	padding-bottom: 100%;
}

#gaze {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

#dist {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}


